<template>
  <div class="channel">
    <Input class="inputtxt" v-model.trim="channel" placeholder="请输入渠道名称" />
    <Button type="primary" @click="createChannel">创建</Button>
    <Table style="margin-top: 30px;" :columns="channelHeader" :data="channelList" />
  </div>
</template>

<script>
  export default {
    name: "channel",
    data(){
      return {
        channel: "",
        channelList: [],
        channelHeader: [
          {
            key: 'id',
            title: 'id',
            align: "center"
          }, {
            title: '渠道名称',
            align: 'center',
            width: 500,
            render: (h, params) =>{
              let state = this.index === params.index, that = this;
              return h("div", [
                h("span", {
                  style: {
                    display: state ? 'none' : 'block'
                  }
                }, params.row.name),
                h("div", [
                  h('Input', {
                    style: {
                      display: state ? 'inline-block' : 'none',
                      width: '300px'
                    },
                    attrs: {
                      value: params.row.name
                    },
                    on: {
                      'on-change': (event) => {
                        that.name = event.target.value
                      },
                    }
                  }),
                  h('Button', {
                    style: {
                      display: state ? 'inline-block' : 'none',
                      margin:" 0 10px"
                    },
                    props: {
                      type: 'primary',
                      size: 'small',
                      ref: 'channel'
                    },
                    on: {
                      click() {
                        that.$Modal.confirm({
                          title: "提示:",
                          content: "确认修改吗？",
                          onOk: () => {
                            that.$axios.put("/channel", {name: that.name || params.row.name, _id: params.row._id})
                              .then(res => {
                                if(res.data.success) {
                                  that.$Message.success(res.data.success);
                                  that.channelList.splice(params.index, 1, res.data.data);
                                  that.index = -1;
                                } else {
                                  that.$Message.info(res.data.err)
                                }
                              });
                          }
                        });
                      }
                    }
                  }, '提交'),
                  h('Button', {
                    style: {
                      display: state ? 'inline-block' : 'none'
                    },
                    props: {
                      type: 'warning',
                      size: 'small'
                    },
                    on: {
                      click() {
                        that.index = -1;
                      }
                    }
                  }, '取消'),
                ])
              ])
            }
          }, {
            title: 'Action',
            align: 'center',
            render: (h, params) => {
              let that = this;
              return h('div', [
                h('Button', {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  on: {
                    click() {
                      that.index = params.index
                    }
                  }
                }, '编辑'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click() {
                      that.$Modal.confirm({
                        title: "提示:",
                        content: "确认删除吗？",
                        onOk: () => {
                          that.$axios.delete("/channel", {params: {id: params.row._id}})
                            .then(res => {
                              that.$Message.success(res.data.success);
                              that.channelList.splice(params.index, 1);
                            });
                        }
                      });
                    }
                  }
                }, '删除')
              ])
            }
          }
        ],
        index: -1,
        name: ''
      }
    },
    methods: {
      getChannelList() {
        this.$axios.get('/channel')
          .then(res => {
            if(res.data.success) {
              this.channelList = res.data.data.reverse();
            }
          })
      },
      createChannel() {
        if(!this.channel) {
          this.$Message.info("渠道名不能为空");
          return;
        }
        this.$Modal.confirm({
          title: "提示:",
          content: "确认创建吗？",
          onOk: () => {
            this.$axios.post("/channel", {name: this.channel})
              .then(res => {
                if(res.data.success) {
                  this.$Message.success(res.data.success);
                  this.channelList.unshift(res.data.data);
                  this.channel = ""
                } else {
                  this.$Message.info(res.data.err);
                }
              });
          }
        });
      }
    },
    mounted() {
      this.getChannelList()
    }
  }
</script>

<style scoped>
  .channel {
    text-align: left;
  }
  .inputtxt {
    width: 300px;
    margin-right: 10px;
  }
</style>